import React from 'react';
import './Main.css';
import { Form, Row, Col, Button } from 'react-bootstrap';

function Main() {
  return (
    <div className="Main">
        <main>

            <div className="template h-100">
              <Row className="h-75 d-flex align-content-center flex-wrap bg-white">
                <Col>
                  <div className="preview_text_div">
                    PREVIEW...
                  </div>
                </Col>
              </Row>
              <div className="preview_code">
                <Row>
                  <Col className="text-left">
                    <Form.Label>CSS code</Form.Label>
                  </Col>
                  <Col className="text-right">
                  <Button variant="outline-dark" className="rounded-0" size="sm">Copy to clipboard</Button>
                  </Col>
                </Row>
                <Form.Control as="textarea" rows={3} className="rounded-0"/>
              </div>
            </div>
        </main>

    </div>
  );
}

export default Main;
